.score-bar {
    display: flex;
    justify-content: space-between;
    font-family: -apple-system,system-ui,BlinkMacSystemFont,Montserrat,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    font-weight: 700;
    font-size: 2vmax;
    text-transform: uppercase;
    color: #fff;
}

.score {
    margin-left: 1rem;
}

.high-score {
    margin-right: 1rem;
}

#win-lose {
    transition: all 300ms ease-in-out;
    display: block;
    opacity: 0;
}